<template>
  <v-container fill-height fluid>
    <div id="welcome">
      <span>Welcome, </span>
    </div>
    <v-row align="center" justify="center">
      <v-card id="register" color="secondary" class="mx-auto my-12 pa-5" elevation="5">
        <h1 class="white--text marker-font">Register</h1><br>
        <form @submit.prevent="Register">
          <v-text-field background-color="white" v-model="email" type="text" label="Email" filled/>
          <v-text-field background-color="white" v-model="password" type="password" label="Password" filled/>
          <v-btn block @click="Register">Register</v-btn>
          &nbsp;
          <p class="white--text">Already have one?
            <router-link to="/login" style="color: #ffffff">Login Here!</router-link>
          </p>
        </form>
      </v-card>
    </v-row>
    <span id="dot"></span>
    <v-snackbar color="red" v-model="snackbar">
      <p class="font-weight-bold  ma-0 pa-0">{{ error }}</p>
      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">Close</v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import firebase from 'firebase';

export default {
  data: function () {
    return {
      snackbar: false,
      error: "",
      email: "",
      password: ""
    }
  },
  methods: {
    Register() {
      const router = this.$router;
      firebase.auth()
          .createUserWithEmailAndPassword(this.email, this.password)
          .then((data) => {
            let welcome = document.getElementById('welcome');
            welcome.innerHTML += (data.user.email.split('@')[0] + "!");
            welcome.style.display = 'block';
            welcome.style.top = '25px';
            let dot = document.getElementById('dot');
            dot.style.width = `${innerWidth * 2}px`;
            dot.style.height = `${innerWidth * 2}px`;
            setTimeout(() => {
              router.replace('/');
            }, 2000);
          })
          .catch((err) => {
            this.error = err;
            this.snackbar = true;
          });
    }
  }
}
</script>

<style>
body {
  overflow: hidden;
}

#welcome {
  text-align: center;
  z-index: 3;
  transition: all 2s;
  color: white;
  display: none;
  position: absolute;
  top: -100px;
  left: 0;
  right: 0;
  font-size: 50px;
}

#register {
  z-index: 2;
}

#dot {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  height: 25px;
  width: 25px;
  background-color: #9DD1F1;
  border-radius: 50%;
  transition: all 1.5s;

}

.marker-font {
  font-family: 'Permanent Marker', cursive;
}
</style>